export class Task {

    public id: number;
    public name: string;
    public completed: boolean;
    public dueDate: string;

    constructor(
        description: string,
        completed: boolean,
        dueDate: string
    ) {
        this.name = description;
        this.completed = completed;
        this.dueDate = dueDate;
    }
}



